.infoBox {
    height: fit-content;
    width: 375px;
    background: rgba(41, 41, 41, 0.971);
    position: absolute;
    bottom: 5%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 20px;
    padding: 25px;
    color: white;
}

.infoContainer {
    display: flex;
    background-color: #0000006b;
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 1000000;
}

.buyLink {
    text-decoration: underline;
    font-weight: 600;
    color: #61fc9f;
}

.buyLink:hover {
    color: adjust-hue($color: #61fc9f, $degrees: 45);
}